
@import url(../common/style/reset.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 16px;
}
.header {
  display: -ms-flexbox;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
  background: #ee7419;
}
a {
  text-decoration: none;
  color: #fff;
}
h1 {
  font-size: 20px;
  color: #fff;
}
h1:before {
    content: '?';
    display: inline-block;
    background: #fff;
    border-radius: 50%;
    color: #ee7419;
    width: 26px;
    height: 26px;
    text-align: center;
    line-height: 26px;
    margin-left: 50px;
}
h2 {
  width: 100px;
  height: 100%;
  font-size: 16px;
}
.main {
  width: 75%;
  margin: 70px auto;
  padding: 20px 10px;
  border: 1px solid #ccc;
  font-size: 20px;
  background: #fff;
  box-shadow: 0 0.05em 0.25em rgba(0, 0, 0, 0.5);
}



