
#calendar {
  position: relative;
}
#calendar #mask {
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0.7;
    height: 100%;
    width: 100%;
    z-index: 10;
}
#calendar .input {
    height: 30px;
    line-height: 30px;
    width: 100px;
    border: 1px solid #888;
    border-radius: 4px;
    cursor: pointer;
    color: #000;
    font-size: 14px;
}
#calendar .date-panel {
    position: absolute;
    width: 280px;
    height: 220px;
    padding: 5px;
    border: 1px solid #ccc;
    background: #fff;
    z-index: 999;
    top: -235px;
    left: 50px;
}
#calendar .date-panel .panel-header {
      height: 40px;
      line-height: 40px;
}
#calendar .date-panel .panel-header .prev {
        display: inline-block;
        width: 0;
        height: 0;
        border-top: 5px solid transparent;
        border-right: 10px solid #222;
        border-bottom: 5px solid transparent;
        cursor: pointer;
}
#calendar .date-panel .panel-header .next {
        display: inline-block;
        width: 0;
        height: 0;
        border-top: 5px solid transparent;
        border-left: 10px solid #222;
        border-bottom: 5px solid transparent;
        cursor: pointer;
}
#calendar .date-panel .panel-header .year {
        padding: 0 5px 0 10px;
}
#calendar .date-panel .panel-header,
      #calendar .date-panel .panel-header .month {
        padding: 0 10px 0 5px;
}
#calendar .date-panel .panel-header .year, #calendar .date-panel .panel-header .month {
        display: inline-block;
        width: 80px;
        height: 20px;
        line-height: 20px;
        margin: 5px;
        vertical-align: middle;
        cursor: pointer;
        font-size: 18px;
}
#calendar .date-panel .type-year {
      width: 100%;
}
#calendar .date-panel .type-year ul li {
        width: 70px;
        height: 60px;
        line-height: 56px;
        font-size: 18px;
}
#calendar .date-panel .type-year ul li:hover {
          background-color: #ee7419;
          border-radius: 5px;
}
#calendar .date-panel .type-month ul li {
      width: 70px;
      height: 60px;
      line-height: 56px;
      font-size: 18px;
}
#calendar .date-panel .type-month ul li:hover {
        background-color: #ee7419;
        border-radius: 5px;
}
#calendar .date-panel .type-date ul li {
      width: 40px;
      height: 26px;
      line-height: 28px;
      font-size: 16px;
}
#calendar .date-panel .type-date ul li date-list li:hover {
        background-color: #ee7419;
        border-radius: 5px;
}
#calendar .date-panel .type-date .preMonth, #calendar .date-panel .type-date .nextMonth {
      color: #ccc;
}
#calendar .date-panel .type-date .selected {
      background-color: #ee7419;
      border-radius: 5px;
}
#calendar .date-panel ul {
      width: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-pack: start;
          justify-content: start;
      list-style: none;
}
#calendar .date-panel ul li {
        width: 40px;
        box-sizing: border-box;
        cursor: pointer;
}
.toggle-enter, .toggle-leave-active {
  opacity: 0;
  transform: translateY(-10px);
}
.toggle-enter-active, .toggle-leave-active {
  transition: all ease .2s;
}



